<template>
  <transition appear
    name="slide-down"
    @before-enter="beforeEnter"
    @enter="enter"
    @before-leave="beforeLeave"
    @leave="leave"
  >
    <slot></slot>
  </transition>
</template>

<script>
export default {
  methods: {
    // enterheight0
    beforeEnter: function (el) {
      el.style.height = "0";
    },
    // enterheight
    enter: function (el) {
      el.style.height = `${el.scrollHeight}px`;
    },
    // leaveheight
    beforeLeave: function (el) {
      el.style.height = `${el.scrollHeight}px`;
    },
    // leaveheight0
    leave: function (el) {
      el.style.height = "0";
    },
  },
};
</script>
