<template>
  <div class="c-container--bg">
    <h2 class="c-container__title"></h2>
    <div class="c-form__wrapper">
      <form class="c-form--large" @submit.prevent="withdraw">
        <p class="c-section__text">
          <br />
          <br />
          <br />
        </p>
        <div class="c-form__button">
          <button type="submit" class="c-btn--danger">
            
          </button>
        </div>
      </form>
      <div class="c-form__link">
        <RouterLink :to="{ name: 'edit' }" class="c-form__link">
          
        </RouterLink>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex"; // VuexmapState

export default {
  computed: {
    ...mapState({
      // authAPI
      apiStatus: (state) => state.auth.apiStatus,
    }),
  },
  methods: {
    // WebAPI
    async withdraw() {
      this.$store.commit("loader/setIsLoading", true); //
      // dispatch()authlogin
      await this.$store.dispatch("auth/withdraw");
      this.$store.commit("loader/setIsLoading", false); //
      // API
      if (this.apiStatus) {
        // 
        this.$store.dispatch("message/showMessage", {
          text: "",
          type: "success",
          timeout: 2000,
        });
        // VueRouterpush()
        this.$router.push({ name: "top" });
      }
    },
  },
};
</script>
