const state = {
  text: '',
  type: '',
  timeoutId: -1,
}

const mutations = {
  // css
  setMessage(state, { text, type }) {
    state.text = text;
    state.type = type;
    // console.log('setMessage');
    // console.log(text);
  },
  // css
  clearMessage(state) {
    state.text = '';
    state.type = '';
    state.timeoutId = -1;
  },
  // timeoutId
  setTimeoutId(state, value) { state.timeoutId = value },
  // timeoutId
  clearTimeoutId(state) { state.timeoutId = -1 },
}

const actions = {
  // 
  showMessage(context, { text, type, timeout }) {
    if (state.timeoutId !== -1) {
      // timeoutId1timeoutIdclearTimeout()
      clearTimeout(state.timeoutId);
      // timeoutId
      context.commit('clearTimeoutId');
    }

    // success
    if (typeof type === 'undefined') {
      type = 'success'
    }
    // 
    context.commit('setMessage', { text, type });

    // 0
    if (timeout === 0) {
      return;
    }

    // 3
    if (typeof timeout === 'undefined') {
      timeout = 5000
    }
    // 
    const timeoutId = setTimeout(() => {
      context.commit('clearMessage')
    }, timeout)
    // setTimeout()timeoutId
    context.commit('setTimeoutId', timeoutId);
  },

  // 
  closeMessage(context) {
    context.commit('clearMessage')
  }
}

// 
export default {
  namespaced: true,
  state,
  mutations,
  actions
}
