          </li>
        </ul>

        <div v-if="showAutoFollow">
          <transition name="popup" appear>
            <AutoFollowList :page="page" />
          </transition>
        </div>
        <div v-else>
          <transition name="popup" appear>
            <TwitterTargetList :page="page" />
          </transition>
        </div>
      </div>

      <div v-if="!useAutoFollow">
        <TwitterTargetList :page="page" />
      </div>
    </section>
  </div>
