<script>
import { mapState, mapGetters } from "vuex"; // VuexmapState,mapGetters
import TwitterLogin from "../components/TwitterLogin.vue";
import TwitterTargetList from "../components/TwitterTargetList.vue";
import AutoFollowList from "../components/AutoFollowList.vue";

export default {
  components: {
    TwitterLogin,
    TwitterTargetList,
    AutoFollowList,
  },
  // router.js
  props: {
    page: {
      type: Number,
      required: false,
      default: 0,
    },
  },
  data() {
    return {
      showAutoFollow: false,
    };
  },
  computed: {
    ...mapGetters({
      // twittercheckTwitter
      isTwitterLogin: "twitter/check",
      // twitteruseAutoFollow
      useAutoFollow: "twitter/useAutoFollow",
    }),
  },
};
</script>
