<template>
  <div>
    <div class="p-autofollow">
      <div class="p-autofollow__guide">
        <p
          class="p-autofollow__status c-alert__inline--success"
          v-if="useAutoFollow"
        >
          <i class="fas fa-mug-hot p-autofollow__icon"></i
          >
        </p>
        <p
          class="p-autofollow__status c-alert__inline--danger"
          v-if="!useAutoFollow"
        >
          <i class="fas fa-ban p-autofollow__icon"></i><br
            class="u-sp--only"
          />
        </p>
      </div>
      <div class="p-autofollow__guide" v-if="useAutoFollow">
        <p class="p-autofollow__text--small">
          1515<br
            class="u-sp--only"
          />11000<br />
        </p>
        <a
          class="c-btn--muted-outline c-btn--small"
          @click.stop="cancelAutoFollow()"
        >
          
        </a>
      </div>

      <div class="p-autofollow__guide" v-if="!useAutoFollow">
        <p class="c-catch u-mb--l p-autofollow__text--small">
          <br />
          <br
            class="u-sp--only"
          />
        </p>
        <button class="c-btn--accent c-btn--large" @click="applyAutoFollow()">
          ON
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex"; // VuexmapState,mapGetters

export default {
  computed: {
    ...mapState({
      // twitterAPI
      apiStatus: (state) => state.twitter.apiStatus,
    }),
    ...mapGetters({
      // twitteruseAutoFollow
      useAutoFollow: "twitter/useAutoFollow",
    }),
  },
  methods: {
    // 
    async applyAutoFollow() {
      // dispatch()twitterapplyAutoFollow
      await this.$store.dispatch("twitter/applyAutoFollow");
      // API
      if (this.apiStatus) {
        // 
        this.$store.dispatch("message/showMessage", {
          text: "",
          type: "success",
          timeout: 2000,
        });
      }
    },
    // 
    async cancelAutoFollow() {
      // dispatch()twittercancelAutoFollow
      await this.$store.dispatch("twitter/cancelAutoFollow");
      // API
      if (this.apiStatus) {
        // 
        this.$store.dispatch("message/showMessage", {
          text: "",
          type: "notice",
          timeout: 2000,
        });
      }
    },
  },
};
</script>
